.c-filter {
  margin: 2rem;
  z-index: 1000;
  padding: 2rem;
  border-radius: 1rem;
  background-color: #fff;
  box-shadow: 0px 0px 20px rgba(53, 75, 96, 0.2);
  width: calc(100% - 4rem);
  color: #354b60;
}

@media (min-width: 576px) {
  .c-filter {
    position: absolute;
    top: 2rem;
    left: 2rem;
    width: 35rem;
  }
}

.c-filter__header {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.c-filter__icon {
  width: 2rem;
  height: 2rem;
}

.c-filter__head-title {
  margin: 0;
  margin-left: 1rem;
  font-weight: 900;
}

.c-filter__form-submit {
  margin-top: 2rem;
  width: 100%;
  display: block;
  padding: 1rem 2rem;
  border: none;
  border-radius: 1rem;
  color: #fff;
  background-color: #354b60;
  opacity: 1;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

.c-filter__form-submit:hover {
  opacity: 0.8;
  transform: scale(1.02);
}

.c-filter__form-items {
  display: none;
  margin-top: 1.5rem;
}

.c-filter__form-item {
  position: relative;
  font-size: 1.6rem;
  padding-bottom: 0.5rem;
}

.c-filter__checkbox {
  width: 2rem;
  height: 2rem;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 1;
  cursor: pointer;
}

.c-filter__checkbox:checked ~ .c-filter__toggle::after {
  opacity: 1;
}

.c-filter__checkbox:checked ~ .c-filter__dropdown {
  display: block;
}

.c-filter__label {
  margin: 0;
  margin-left: 3.5rem;
  display: inline-block;
}

.c-filter__toggle {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border: 0.3rem solid #354b60;
  border-radius: 0.5rem;
  position: absolute;
  top: 0.2rem;
}

.c-filter__toggle::after {
  content: "";
  width: 1.2rem;
  height: 2rem;
  background-image: url(../img/check.svg);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 0.3rem;
  left: 0.1rem;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.c-filter__dropdown {
  display: none;
  font-size: 1.4rem;
  color: #354b60;
  margin-left: 3.5rem;
}

.c-filter__searchbar {
  outline: none;
  margin: 0.5rem 0;
  padding: 0.5rem 1rem;
  border: 0.3rem solid #354b60;
  border-radius: 0.5rem;
  width: 23rem;
}

@media (max-width: 767.98px) {
  .c-filter__searchbar {
    width: 20rem;
  }
}

.c-filter__search-results {
  display: flex;
  flex-direction: column;
  margin: 0.2rem 0.5rem;
}

.c-filter__search-result-item {
  display: flex;
  justify-content: space-between;
  font-size: 1.4rem;
  color: #354b60;
  text-decoration: none;
  border: none;
  margin: 0.5rem;
  cursor: pointer;
  transition: color 0.3s ease;
}

.c-filter__search-result-item::after {
  content: "";
  display: block;
  width: 2rem;
  height: 1rem;
  background-image: url(../img/pijl-blauw.svg);
  background-size: contain;
  background-repeat: no-repeat;
  transform: translateY(50%);
  margin-left: 2rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.c-filter__search-result-item:hover {
  text-decoration: none;
  border: none;
  color: #00B8B1;
}

.c-filter__search-result-item:hover::after {
  opacity: 1;
}

.c-filter__search-result-item:hover::before {
  text-decoration: none;
  border: none;
}

.c-filter__range {
  padding-top: 3rem;
}

.c-filter__range input[type="range"] {
  position: absolute;
  pointer-events: none;
  -webkit-appearance: none;
  z-index: 2;
  height: 1rem;
  width: 93%;
  opacity: 0;
  z-index: 1000;
}

.c-filter__range--multirange-left {
  right: 7%;
}

.c-filter__range--multirange-right {
  left: 7%;
}

.c-filter__range input[type="range"]::-webkit-slider-thumb {
  pointer-events: all;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 0;
  border: 0 none;
  background-color: red;
  -webkit-appearance: none;
}

.c-filter__range--middle {
  position: relative;
  max-width: 28rem;
}

.c-filter__range--slider {
  position: relative;
  z-index: 1;
  height: 3px;
  margin: 0 15px;
}

.c-filter__range--slider__track {
  position: absolute;
  z-index: 1;
  left: -1rem;
  right: -1rem;
  top: 0;
  bottom: 0;
  border-radius: 5px;
  background-color: #354b60;
}

.c-filter__range--slider__range {
  position: absolute;
  z-index: 2;
  left: 25%;
  right: 25%;
  height: calc(100% + 2px);
  top: -1px;
  bottom: 0;
  border-radius: 5px;
  background-color: #00B8B1;
}

.c-filter__range--slider__thumb {
  position: absolute;
  z-index: 3;
  width: 1.2rem;
  height: 1.2rem;
  background-color: #00B8B1;
  border-radius: 50%;
  box-shadow: 0 0 0 0 rgba(0, 184, 177, 0.1);
  transition: box-shadow 0.3s ease-in-out;
}

.c-filter__range--slider__thumb--left {
  left: 25%;
  transform: translate(-15px, -5px);
}

.c-filter__range--slider__thumb--left::after {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  content: attr(data-left);
  color: #000000;
  transform: translateX(-50%);
}

.c-filter__range--slider__thumb--right {
  right: 25%;
  transform: translate(15px, -5px);
}

.c-filter__range--slider__thumb--right::after {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  content: attr(data-right);
  color: #000000;
  transform: translateX(-50%);
}

.c-filter__range--slider__thumb.hover {
  box-shadow: 0 0 0 10px rgba(0, 184, 177, 0.1);
}

.c-filter__range--slider__thumb.active {
  box-shadow: 0 0 0 20px rgba(0, 184, 177, 0.2);
}

.wereldcirkel {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  position: absolute;
  animation: wereldcirkeldraaien 35s linear alternate infinite;
  background-image: url(../img/wereldkaart.svg);
  background-size: 600%;
  background-position-y: center;
  background-repeat: no-repeat;
  border: #354B60 4px solid;
  display: inline-block;
  margin-left: 2rem;
}

@keyframes wereldcirkeldraaien {
  100% {
    background-position: 100%;
  }
}
