.c-kleurkaart {
    width: 100%;
    height: 90vh;
    @include media-breakpoint-down(sm) {
      height: 50vh;
    }
  
    .info {
      padding: 6px 8px;
      font: 14px/16px "Mulish", sans-serif;
      background: white;
      background: rgba(255, 255, 255, 0.8);
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
      border-radius: 5px;
      &.leaflet-control {
        font-size: 2rem;
        .info-panel {
          padding-bottom: 0.5rem;
          &--bottom {
            padding-top: 0.3rem;
          }
        }
      }
    }
    .info h4 {
      margin: 0 0 5px;
      color: #777;
    }
  
    .info.legend {
      @include media-breakpoint-down(sm) {
        font-size: 12px;
      }
    }
  
    .leaflet-control-zoom {
      box-shadow: none;
    }
  
    .legend--background {
      width: 20px;
      height: 20px;
      @include media-breakpoint-down(sm) {
        width: 15px;
        height: 15px;
      }
    }
    .leaflet-control-attribution {
      a {
        font-size: 1.2rem;
      }
    }
  }
  .js-countryimg {
    box-shadow: 1px -1px 3px 2px rgba($color-black, 0.1);
    height: 3rem;
    width: auto;
  }